  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Onest', sans-serif;
}

img {
  display: block;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding-left: 16px;
  padding-right: 16px;
}

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  display: flex;
  justify-content: start;
  align-items: center;
  gap: 16px;
}

.logo img {
  border-radius: 100%;
}

.logo span {
  font-weight: 800;
  font-size: 32px;
  color: #252526;
}

header {
  padding: 24px 16px;
}

nav #hamburger {
  background-color: transparent;
  border: none;
}

nav .hamburger {
  display: block;
  width: 48px;
  padding: 4px;
  border: 1px solid #e0e0e0;
  border-radius: 8px;
}

nav ul {
  display: none;
}

nav ul li {
  list-style: none;
}

nav ul li a {
  text-decoration: none;
  padding: 16px;
  display: inline-block;
  color: #5b5c5e;
  font-size: 24px;
}

nav ul li:hover a {
  color: #141414;
}

.text-orange {
  color: #ff8000 !important;
}

.font-semibold {
  font-weight: 600;
}

.hero {
  height: 400px;
  width: 100%;
  margin: 0 auto;
  overflow: hidden;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-size: cover;
  background-repeat: no-repeat;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  padding: 0px 16px;
}

.hero::before {
  content: "";
  background-color: #141414;
  opacity: 0.5;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.hero .tagline {
  color: #ffffff;
  font-size: 48px;
  z-index: 2;
}

.restos {
  text-align: center;
}

.restos h2 {
  font-size: 32px;
  margin: 24px 0 24px;
}

.resto-cards {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr;
  gap: 32px;
  text-align: left;
}

.resto-item {
  border-start-start-radius: 12px;
  overflow: hidden;
}

.resto-item .desc {
  height: 160px;
  overflow: hidden;
}

.thumbnail-item {
  position: relative;
}

.thumbnail-item .label {
  position: absolute;
  top: 24px;
  z-index: 2;
  padding: 8px 16px 8px 8px;
  background: #ff8000;
  color: #ffffff;
  border-end-end-radius: 50px;
  border-start-end-radius: 50px;
  font-size: 24px;
}

.resto-item img {
  width: 100%;
  height: 250px;
  position: relative;
}

.resto-item .desc .title {
  font-size: 24px;
}

.resto-item .desc .rating {
  display: inline-block;
  margin: 8px 0px;
  background-color: #ffeec2;
  color: #ff5e00;
  padding: 4px 8px;
  border-radius: 8px;
}

footer {
  text-align: center;
  margin-top: 40px !important;
  padding: 40px;
}

.showNav {
  display: flex;
  flex-direction: column;
  gap: 6px;
  background: #ffffff;
  padding: 24px;
  border-radius: 24px;
  z-index: 10;
  text-align: right;
  position: absolute;
  right: 16px;
}

.skip-link {
  position: absolute;
  top: -40px;
  left: 0;
  background-color: #ff8000;
  color: white;
  padding: 8px;
  z-index: 100;
}

.skip-link:focus {
  top: 0;
}


@media screen and (min-width: 768px) {
  .resto-cards {
    grid-template-columns: repeat(2, 1fr);
  }

  .hero {
    max-width: 100%;
  }
}

@media screen and (min-width: 1024px) {
  .container {
    max-width: 1200px;
  }

  .resto-cards {
    grid-template-columns: repeat(3, 1fr);
  }

  nav #hamburger {
    display: none;
  }

  nav ul {
    display: flex;
    gap: 16px;
  }

  nav ul li a {
    font-size: 16px;
  }

  .hero {
    width: 1200px;
  }
